.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 480px) and (max-width: 767px) {
    width: 480px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 768px;
    // padding-left: 34px;
    // padding-right: 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    max-width: 1600px;
    // padding-left: 116px;
    // padding-right: 116px;
  }
}
